




























































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Emit } from 'vue-property-decorator';
import { Route, RouteConfig } from 'vue-router';
import Scrollbar from 'element-ui/lib/scrollbar';
@Component({
  components: {
    elScrollbar: Scrollbar,
  },
})
export default class SideBar extends Vue {
  @Prop({ default: '#FF6551' }) private color!: string;

  @Prop({ default: () => [], required: true }) private menus!: RouteConfig[];

  @Prop() private active!: Route;

  @Prop({ default: false }) private isCustom!: boolean;


  private collapse: boolean = false;

  @Emit()
  private route(route: RouteConfig): RouteConfig {
    return route;
  }

  @Emit()
  private open(index: string, path: string[]) {
    return { index, path };
  }

  @Emit()
  private close(index: string, path: string[]) {
    return { index, path };
  }

  @Emit()
  private select(index: string, path: string[]) {
    return { index, path };
  }

  private toggleCollapse() {
    this.collapse = !this.collapse;
  }

  private get defaultOpeneds() {
    const opened: RouteConfig = this.menus.find((menu: RouteConfig) => {
      if (menu.children) {
        return !!menu.children.find(
          (child: RouteConfig) => this.active.name === child.name
          // return !this.active.path.indexOf(child.path);
          ,
        );
      }
      return false;
    }) || { path: '/' };
    return [opened.path || '/'];
  }

  private link(route: RouteConfig) {
    /* alert(this.active.path)
       this.$router.push(route.path); */
    if (this.isCustom) {
      // 如果不是默认行为
      return this.route(route);
    }

    if (route.name) {
      this.$router.push({ name: route.name });
    } else {
      this.$router.push({ path: route.path });
    }
    return '';
  }
}
