


































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

@Component
export default class LayoutContainer extends Vue {
  @Prop({ default: true }) private isHeader!: boolean;

  @Prop({ default: true }) private isTabsBar!: boolean;

  @Prop({ default: true }) private isSideBar!: boolean;

  @Prop({ default: false }) private isCustom!: boolean;
}
