






































































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch, Emit } from 'vue-property-decorator';
// import { Action, State, Mutation } from 'vuex-class';
import _, { debounce, throttle } from 'lodash';
import { Route } from 'vue-router';
// import Base from '../../base';
@Component
export default class TabsBar extends Vue {
  @Prop({ default: '/' }) private home!: string;

  @Prop({ default: '' }) private homeText!: string;

  @Prop({ default: '' }) private keep!: string;

  @Prop({ default: () => [] }) private menus!: Route[];

  @Prop() private active!: Route;

  private isSlide: boolean = false;

  private isShowOpts: boolean = false;

  private navOffset: number = 0;

  @Emit()
  private close(command: string, index?: number): {
    command: string;
    index?: number;
  } {
    this.detectSlide();
    return { command, index };
  }

  @Watch('active', { immediate: true })
  private activeChange(newVal: Route) {
    this.detectSlide();
    setTimeout(() => {
      this.scrollToActiveTab();
    }, 300);
  }

  @Watch('isSlide', { immediate: true })
  private slideChange(newVal: boolean) {
    setTimeout(() => {
      this.scrollToActiveTab();
    }, 300);
  }

  private detectSlide() {
    setTimeout(() => {
      const prevIsSlide = this.isSlide;
      const { container, items } = this.tabsEleWidthLeft();
      this.isSlide = items.width > container.width;
      if (prevIsSlide && !this.isSlide) {
        this.slide('prev');
      }
    }, 50);
  }

  private scrollToActiveTab() {
    if (!this.slide) {
      return;
    }
    const container = document.querySelector('#TB__Container') as HTMLElement;
    const transitionEle = document.querySelector('#TB__Items') as HTMLElement;
    const activeTab = (document.querySelector('#TB__Active') as HTMLElement);

    if (!activeTab) {
      return;
    }

    const activeTabBounding = activeTab.getBoundingClientRect();
    const navScrollBounding = container.getBoundingClientRect();
    const maxOffset = transitionEle.offsetWidth - navScrollBounding.width;
    const currentOffset = this.navOffset;
    let newOffset = currentOffset;

    if (activeTabBounding.left - 70 < navScrollBounding.left) {
      newOffset = currentOffset - (navScrollBounding.left - activeTabBounding.left) - 70;
    }
    if (activeTabBounding.right + 70 > navScrollBounding.right) {
      newOffset = currentOffset + activeTabBounding.right - navScrollBounding.right + 70;
    }
    newOffset = Math.max(newOffset, 0);
    this.navOffset = Math.min(newOffset, maxOffset);
  }

  private slide(arrow: string) {
    const itemsScroll: HTMLElement = document.querySelector('#TB__Items') as HTMLElement;
    const containerWidth = (document.querySelector('#TB__Container') as HTMLElement).clientWidth;
    const itemsWidth = itemsScroll.clientWidth;
    switch (arrow) {
      case 'prev':
        this.navOffset = 0;
        break;
      case 'next':
        this.navOffset = itemsWidth - containerWidth;
        break;
      default:
        break;
    }
  }

  private tabsEleWidthLeft(): {
    container: { ele: HTMLElement, width: number },
    items: { ele: HTMLElement, width: number },
    } {
    const items: HTMLElement = document.querySelector('#TB__Items') as HTMLElement;
    const itemsWidth = items ? items.clientWidth : 0;

    const container: HTMLElement = document.querySelector('#TB__Container') as HTMLElement;
    const containerWidth = container ? container.clientWidth : 0;

    return {
      container: {
        ele: container,
        width: containerWidth,
      },
      items: {
        ele: items,
        width: itemsWidth,
      },
    };
  }

  private handleOptsBackGround(is: boolean) {
    this.isShowOpts = is;
  }

  private mounted(): void {
    this.$nextTick(() => {
      this.detectSlide();
      window.addEventListener('resize', throttle(() => {
        this.detectSlide();
        this.scrollToActiveTab();
      }, 500));
      // 监听左侧菜单的 WIDTH 更改时，做出相应的是否需要 SLIDE 按钮
      document.addEventListener('transitionend', ((event: TransitionEvent) => {
        if (event.propertyName === 'width') {
          this.detectSlide();
          this.scrollToActiveTab();
        }
      }) as any);
    });
  }

  private handleDropdownItemClick(command: string) {
    switch (command) {
      case 'others':
        this.close('others', this.menus.findIndex((menu: Route) => menu.path === this.active.path));
        break;
      case 'all':
        this.close('all');
        break;
      default:
        break;
    }
  }
}
