






























import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch, Emit } from 'vue-property-decorator';

@Component
export default class TitleBar extends Vue {
  @Prop({ default: () => ['标签'] }) private tabs!: string[];

  @Prop({ default: 0 }) private active!: number;

  @Prop({ default: '#37b69c' }) private color!: string;

  @Emit()
  private choose(index: number): number {
    return index;
  }

  private tab(index: number) {
    const ele: HTMLElement = (this.$refs.tabsBar as HTMLDivElement)
      .querySelectorAll('.item')[index] as HTMLElement;
    const arrow = (this.$refs.tabsArrow as HTMLElement);
    arrow.style.width = `${ele.clientWidth}px`;
    arrow.style.left = `${ele.offsetLeft}px`;
    this.choose(index);
  }

  private mounted() {
    this.$nextTick(() => {
      this.tab(0);
    });
  }
}
