













































import Vue from 'vue';
import Component from 'vue-class-component';
import { RouteConfig, Route } from 'vue-router';
import { Watch } from 'vue-property-decorator';
import LayoutContainer from '../packages/LayoutContainer';
import SideBar from "../packages/SideBar";
import TabsBar from "../packages/TabsBar";
import Routes from './router/routes';
@Component({
  components: {
    LayoutContainer,
    SideBar,
    TabsBar,
  }
})
export default class Layout extends Vue {
  private isShowInterActive = true;

  private unread = 89;

  private appLink = '#';

  private showDialog = false;

  private headerData: any = {
    system: {
      name: '这是系统名字',
      // pic: systemLogo,
    },
    company: {
      name: '这是品牌名称',
    },
    user: {
      name: '这是用户名',
      pic: 'http://img2.imgtn.bdimg.com/it/u=3377302992,3361149372&fm=26&gp=0.jpg',
    },
  };

  private langChange() {
    sessionStorage.lang = sessionStorage.lang === 'en' ? 'zhCN' : 'en';
    this.$router.go(0);
  }

  // 首页始终指向当前页面
  private keepTab: string = (Routes[1] as any).children[0].name;

  private routes: RouteConfig[] = [];

  private tabsRoutes: Route[] = (localStorage.tabsRoutes
    && JSON.parse(localStorage.tabsRoutes))
    || [];

  private activeRoute: any = {
    path: '/',
    fullPath: '/',
    name: 'Index',
    hash: '',
    params: {},
    query: {},
  };

  @Watch('$route', { immediate: true, deep: true })
  private $routeChange(newVal: Route, oldVal: Route) {
    const {
      name, path, meta, hash, query, params, fullPath, matched,
    } = newVal;
    this.activeRoute = {
      name, path, meta, hash, query, params, fullPath,
    };

    const isExistTabs = this.tabsRoutes.find((tab: Route) => tab.path === this.activeRoute.path);

    if (this.activeRoute.path === '/' && this.keepTab) {
      return this.$router.push({ name: this.keepTab });
    }
    // 去除首页挂载
    if (!isExistTabs && this.activeRoute.path !== '/') {
      this.tabsRoutes.push(this.activeRoute);
      localStorage.tabsRoutes = JSON.stringify([...this.tabsRoutes]);
    }
    return '';
  }

  private closeTab(opt: { command: string; index?: number }) {
    const { command, index } = opt;
    const keep = this.tabsRoutes.filter((tab: Route) => tab.name === this.keepTab);
    switch (command) {
      case 'current': {
        const i = index as number;
        const currRoute = this.tabsRoutes[i];
        const activeIndex = this.tabsRoutes.findIndex(
          (tab: Route) => tab.path === this.activeRoute.path,
        );
        if (i === activeIndex) {
          this.$router.push({ path: this.tabsRoutes[i ? i - 1 : i + 1].path });
        }
        this.tabsRoutes.splice(i, 1);
        localStorage.tabsRoutes = JSON.stringify([...this.tabsRoutes]);
        break;
      }
      case 'others':
        this.tabsRoutes = this.tabsRoutes.splice(index as number, 1);
        localStorage.tabsRoutes = JSON.stringify([...keep, ...this.tabsRoutes]);
        break;
      case 'all':
        this.tabsRoutes = [...keep];
        localStorage.tabsRoutes = JSON.stringify([...this.tabsRoutes]);
        // localStorage.removeItem('tabsRoutes');
        this.$router.push({ path: '/' });
        break;
      default:
        break;
    }
  }

  private handleSideBarOpen(obj: { index: number, path: string }) {
    console.log(obj);
  }

  private sideRoute(route: RouteConfig) {
    if (route.name) {
      this.$router.push({ name: route.name });
    } else {
      this.$router.push({ path: route.path });
    }
  }

  private created() {
    // 获取路由配置信息
    this.routes = JSON.parse(JSON.stringify(Routes));
  }

  private handleItemClick(obj: any) {
    console.log('1', obj);
    this.showDialog = true;
  }

  private handleDialogClose() {
    this.showDialog = false;
  }

  static handlePrev() {
    console.log('prev');
  }

  static handleNext() {
    console.log('next');
  }
}
